import router from "next/router";

{/* Login Card - Refined Design */}
<div
  className="
  /* Mobile */
  flex flex-col justify-center items-center bg-white rounded-3xl shadow-xl p-5 w-full
  
  /* Tablet */
  sm:p-6 sm:max-w-md
  
  /* Desktop */
  md:w-[28rem] md:p-7
"
>
  <div className="w-full">
    <h2
      className="
      /* Mobile */
      text-2xl font-bold text-green-900 mb-5 text-center
      
      /* Tablet */
      sm:text-2xl sm:mb-6
      
      /* Desktop */
      md:text-2xl md:mb-7
    "
    >
      Login
    </h2>

    {/* Login Form - Consistent Spacing */}
    <div className="space-y-4">
      <div>
        <label
          htmlFor="username"
          className="
          /* Mobile */
          block text-sm font-medium text-[#013300] mb-1.5
          
          /* Tablet */
          sm:text-[15px]
        "
        >
          Username
        </label>
        <input
          id="username"
          type="text"
          placeholder="Enter your username"
          className="
            /* Mobile */
            w-full px-4 py-2 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#013300] focus:border-transparent transition placeholder-gray-400 text-[#013300]
            
            /* Tablet */
            sm:py-2.5
          "
        />
      </div>

      <div>
        <label
          htmlFor="password"
          className="
          /* Mobile */
          block text-sm font-medium text-[#013300] mb-1.5
          
          /* Tablet */
          sm:text-[15px]
        "
        >
          Password
        </label>
        <input
          id="password"
          type="password"
          placeholder="Enter your password"
          className="
            /* Mobile */
            w-full px-4 py-2 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#013300] focus:border-transparent transition placeholder-gray-400 text-[#013300]
            
            /* Tablet */
            sm:py-2.5
          "
        />
      </div>

      <div className="flex items-center justify-between pt-2 pb-4">
        <div className="flex items-center">
          <input
            id="remember-me"
            name="remember-me"
            type="checkbox"
            className="
              /* Mobile */
              h-4 w-4 text-green-600 focus:ring-[#013300] border-gray-300 rounded
            "
          />
          <label
            htmlFor="remember-me"
            className="
            /* Mobile */
            ml-2 block text-sm text-gray-700
            
            /* Tablet */
            sm:text-[15px]
          "
          >
            Remember me
          </label>
        </div>
        <div className="text-sm sm:text-[15px]">
          <a href="#" className="font-medium text-green-600 hover:text-green-500">
            Forgot password?
          </a>
        </div>
      </div>

      <button
        type="button"
        className="
          /* Mobile */
          w-full bg-gradient-to-r from-green-600 to-[#133000] text-white font-bold py-2.5 rounded-lg hover:opacity-90 transition shadow-md
          
          /* Tablet */
          sm:py-2.5
        "
        onClick={() => router.push("/Proponent/welcome")}
      >
        Login
      </button>

      {/* Divider */}
      <div className="relative my-4">
        <div className="absolute inset-0 flex items-center">
          <div className="w-full border-t border-gray-300"></div>
        </div>
        <div className="relative flex justify-center text-sm sm:text-[15px]">
          <span className="px-2 bg-white text-gray-500">or continue with</span>
        </div>
      </div>

      {/* Social Login */}
      <button
        className="
        /* Mobile */
        flex items-center justify-center w-full border border-gray-300 rounded-lg py-2 hover:bg-gray-50 transition
        
        /* Tablet */
        sm:py-2.5
      "
      >
        <img src="/GoogleLogo.png" alt="Google" className="w-5 h-5 mr-3" />
        <span className="font-medium text-gray-700">Google</span>
      </button>

      {/* Sign up link */}
      <div
        className="
        /* Mobile */
        mt-5 text-center text-sm
        
        /* Tablet */
        sm:text-[15px]
      "
      >
        <span className="text-gray-600">Don't have an account? </span>
        <a href="#" className="font-medium text-green-600 hover:text-green-500">
          Sign up
        </a>
      </div>
    </div>
  </div>
</div>